                                <div class="col-7 px0">
                                    <input type="text" class="form-control" v-model="supplier.supplier_contact"/>
                                </div>
                            </div> 
                            <div class="col-12 cprow flex">
                                <div class="col-5 px0">
                                    <label>Supplier Address</label>
                                </div>
                                <div class="col-7 px0">
                                    <input type="text" class="form-control" v-model="supplier.supplier_address"/>
                                </div>
                            </div>  
                            <div class="col-12 cprow flex">
                                <div class="col-5 px0">
                                    <label>Maintenance</label>
                                </div>
                                <div class="col-7 px0">
                                    <input type="text" class="form-control" v-model="action.maint_details"/>
                                </div>
                            </div>       
                        </div>
                        <div class="col-4 px0">
                            <div class="col-12 cprow flex">
                                <div class="col-5 px0">
                                    <label>Invoice Number</label>
                                </div>
                                <div class="col-7 px0">
                                    <input type="text" class="form-control" v-model="purchase.invoice"/>
                                </div>
                            </div>
                            <div class="col-12 cprow flex">
                                <div class="col-5 px0">
                                    <label>Purchase Date</label>
