                            </div> 
                            <div class="col-12 cprow flex">
                                <div class="col-5 px0">
                                    <label>Reimbursement ID</label>
                                </div>
                                <div class="col-7 px0">
                                    <input type="text" class="form-control" v-model="procurement.quot" @keyup="search_reimbursement"/>
                                </div>
                            </div>
                            <div v-if="scope=='bulk'" class="col-12 cprow flex">
                                <div class="col-5 px0">
                                    <label>Variance</label>
                                </div>
                                <div class="col-7 px0">
                                    <input type="text" class="form-control" v-model="purchase.variance"/>
                                </div>
                            </div> 
                            <div class="col-12 cprow flex">
                                <div class="col-5 px0">
                                    <label>Fund Source</label>
                                </div>
                                <div class="col-7 px0">
                                    <input type="text" class="form-control" v-model="procurement.fundsrc"/>
                                </div>
                            </div> 
                            <div class="col-12 cprow flex">
                                <div class="col-5 px0">
                                    <label>Unit Cost</label>
                                </div>
                                <div class="col-7 px0">
                                    <input type="float" class="form-control" v-model="procurement.unit_cost" min="0.00"/>
                                </div>
                            </div>   
                            <div v-if="scope=='single'" class="col-12 cprow flex">
                                <div class="col-5 px0">
                                    <label>Fund Source (extra)</label>
                                </div>
                                <div class="col-7 px0">
                                    <input type="text" class="form-control" v-model="procurement.fundsrc_2"/>
                                </div>
                            </div> 
                            <div v-if="scope=='single'" class="col-12 cprow flex">
                                <div class="col-5 px0">
                                    <label>Unit Cost (extra)</label>
                                </div>
                                <div class="col-7 px0">
                                    <input type="float" class="form-control" v-model="procurement.unit_cost_2" min="0.00"/>
                                </div>
                            </div> 
                            <div class="col-12 cprow flex">
                                <div class="col-5 px0">
                                    <label>Quantity</label>
                                </div>
                                <div class="col-7 px0">
                                    <input type="number" class="form-control" v-model="purchase.quantity" min="1"/>
                                </div>
                            </div> 
                            <div class="col-12 cprow flex">
                                <div class="col-5 px0">
                                    <label>Total Amount</label>
                                </div>
                                <div class="col-7 px0">
                                    <input type="float" class="form-control" v-model="purchase.total_cost" min="0.00"/>
                                </div>
                            </div>                      
                        </div>
                    </div>
                </div>
                <div class="card-footer">
                    <div class="flex">
                        <button type="submit" class="btn btn-primary btn-sm">
                            <i class="fa fa-plus-circle"></i> Update
                        </button>                   
                        <button type="button" class="btn btn-danger btn-sm" @click="return_prev">
                            <i class="fa fa-refresh"></i> Return
                        </button>
                        <button type="button" class="btn btn-danger btn-sm" @click="write_off" style='margin-left:auto!important'>
                            <i class="fa fa-times"></i> Request Write Off
                        </button>
                    </div>
                </div>
            </div>
        </form>
        <!--Panel Box overlay-->
        <div class="inner_overlay">
            <div class="cross_box">
                <i class="fa fa-window-close" aria-hidden="true" style='margin-left:auto;margin-top:0.25rem;margin-right:0.5rem;cursor:pointer' @click="close_overlay"></i>
            </div>
            <!--Supplier-->
            <v-data-table
                :headers="headers"
                :items="supplier_list"
                class="elevation-1"
                :rows-per-page-items="[3]"
                v-if="hv_supplier"
                >
            <template v-slot:items="props">
                <tr v-on:click="select_sup(props.item.name,props.item.loc,props.item.contact_num)" style='cursor:pointer'>
                    <td>Select</td>
                    <td>{{ props.item.name }}</td>
                    <td>{{ props.item.loc }}</td>
                    <td>{{ props.item.contact_num }}</td>
                </tr>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table> 
            <!--[END]Supplier-->
            <!--Reimbursement-->
            <v-data-table
                :headers="headers_reimb"
                :items="reimb_list"
                class="elevation-1"
                :rows-per-page-items="[3]"
                v-if="hv_reimb"
                >
            <template v-slot:items="props">
                <tr 
                v-on:click="
                select_reimb(
                    props.item.receiver,
                    props.item.address,
                    props.item.company_name,
                    props.item.contact,
                    props.item.tag,
                    props.item.nature,
                    props.item.total,
                    props.item.invoice_num,
                    props.item.fundsrc,
                    props.item.reason
                    )" 
                style='cursor:pointer'>
                    <td>Select</td>
                    <td>{{ props.item.tag }}</td>
                    <td>{{ props.item.nature }}</td>
                    <td>{{ props.item.invoice_num }}</td>
                    <td>{{ props.item.fundsrc }}</td>
                    <td>{{ props.item.total }}</td>
                </tr>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table> 
            <!--[END]Reimbursement-->
            <!--Procurement-->
            <v-data-table
                :headers="headers_proc"
                :items="proc_list"
                class="elevation-1"
                :rows-per-page-items="[3]"
                v-if="hv_proc"
                >
            <template v-slot:items="props">
                <tr 
                v-on:click="select_proc(props.item.proc_id,props.item.dept,props.item.record_descript,props.item.category)" 
                style='cursor:pointer'>
                    <td>Select</td>
                    <td>{{ props.item.dept_full }}</td>                   
                    <td>{{ props.item.category }}</td>                   
                    <td>{{ props.item.record_descript }}</td>
                    <td>{{ props.item.proc_id }}</td>
                </tr>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table> 
            <!--[END]Procurement-->
        </div>
        <!--[END]Panel Box overlay-->
    </div>
</template>
<script>
export default {
    data(){
        return{
            grp_list:[],
            hv_supplier:false,
            supplier_list:[],
            headers: [
                { text: 'Action', value :'action',sortable: false},
                { text: 'Name', value:'name',sortable: false },
                { text: 'Address', value:'loc',sortable:false },
                { text: 'Contact', value:'contact_num',sortable:false }
            ], 
            hv_reimb:false,
            reimb_list:[],
            headers_reimb: [
                { text: 'Action', value :'action',sortable: false},
                { text: 'Tag', value:'tag',sortable: false },
                { text: 'Invoice', value:'invoice_num',sortable:false },
                { text: 'Purchase', value:'nature',sortable:false },                               
                { text: 'Fund Source', value:'fundsrc',sortable:false },
                { text: 'Total', value:'total',sortable:false }
            ],   
            hv_proc:false,
            proc_list:[],
            headers_proc: [
                { text: 'Action', value :'action',sortable: false},
                { text: 'Department', value:'dept_full',sortable:false },
                { text: 'Category', value:'category',sortable:false },
                { text: 'Title', value:'record_descript',sortable:false },
                { text: 'Tag', value:'proc_id',sortable: false }          
            ],         
            proc_list:[],
            scope:'',
            ready:false,
            item_code:'',
            details:[],
            category_list:[],
            sub_category_list:[],
            blks:['A','B','C','D','E','F','G','H'],
            index_sup:['st','nd','rd','th','th','th','th','th'],
            code:{
                barcode:'',
                sn:'',
                category:'',
                sub_category:'All',
            },
            location:{
                grp_code:'',
                building_code:'',
                location_code:'',
                sub_location_code:''
            },
            product:{
                eng_name:'',
                chi_name:'',
                eng_dp:'',
                chi_dp:'',   
                brand:'',   
                license:'',   
                ownership:0,     
            },
            purchase:{
                purchase_date:'',
                warranty_exp:'',
                quantity:0,
                total_cost:0.00,
                invoice:'',
                variance:'',
            },
            supplier:{
                name:'',
                supplier_contact:'',
                supplier_address:'',               
            },
            procurement:{
                quot:'',
                tender:'',
                fundsrc:'',
                unit_cost:0.00,
                fundsrc_2:'',
                unit_cost_2:0.00
            },
            action:{
                remarks:'',
                stocktake:'',
                tag:'',
                maint_details:'',
            }
        }
    },
    async created(){
        this.scope = document.getElementById('tmp_id_2').value;
        this.item_code = document.getElementById('tmp_id').value;
        await this.load_filter();
        await this.fetch_details();
        await this.fetch_sub_category();
        this.ready = true;
    },
    async mounter(){

    },
    methods:{
        async write_off(){
            if(this.action.tag==""){
                this.$alert('Please fill in write off reason');
            } else {
                this.$confirm('Confirm Write Off item :'+this.item_code+' ?')
                    .then(
                        async()=>{
                            var formData = new FormData();
                            formData.append('item_code',this.item_code);
                            formData.append('reason',this.action.tag);
                            formData.append('eng_name',this.product.eng_name);
                            if(this.scope=='single'){
                                formData.append('details',this.code.sn);
                            }
                            await axios.post('inventory/web/backend/apply_write_off.php',formData)
                                .then(  
                                    (response)=>{
                                        if(response.data=="OK"){
                                            this.$alert('Successfully applied write off for item :'+this.item_code);
                                            this.$router.push('inventory_list');
                                        }
                                    }
                                )
                        }
                    )
            }
        },
        search_records(type){
            this.hv_supplier = false;
            this.hv_reimb = false;
            this.hv_proc = false;
            if(type=='supplier'){
                this.search_supplier();
            } else if(type=='reimb'){
                this.search_reimbursement();
            } else if(type=='proc'){
                this.search_procurement();
            }
        },
        close_overlay(){
            this.hv_proc = false;
            this.hv_reimb = false;
            this.hv_supplier = false;
        },  
        select_sup(name,loc,contact){
            this.supplier.name = name;
            this.supplier.supplier_contact = contact;
            this.supplier.supplier_address = loc;
            this.hv_supplier = false;
        },
        select_reimb(receiver,address,company_name,contact,tag,nature,total,invoice_num,fundsrc,reason){
            this.procurement.quot = tag;
            this.purchase.total_cost = total;
            this.procurement.unit_cost = (total)/(this.purchase.quantity);
            this.product.eng_name = nature;
            this.purchase.invoice = invoice_num;
            this.procurement.fundsrc = fundsrc;
            this.product.eng_dp = reason;
            if(receiver=='company'){
                this.supplier.name = company_name;
                this.supplier.supplier_address = address;
                this.supplier.supplier_contact = contact;
            }
            this.hv_reimb = false;
        },
        async select_proc(proc_id,dept,record_descript,category){
            this.procurement.tender = proc_id;
            this.hv_proc = false;
            await this.proc_reimb(proc_id);

        },  
        async proc_reimb(proc_id){
            var formData = new FormData();
            formData.append('proc_id',proc_id);
            await axios.post('inventory/web/backend/search_proc_reimb.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.reimb_list = response.data;
                            this.hv_reimb = true;
                        } else {
                            this.hv_reimb = false;
                        }
                    }
                )
        },
        async search_reimbursement(){
            var formData = new FormData();
            formData.append('key',this.procurement.quot);
            await axios.post('inventory/web/backend/search_reimb.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.reimb_list = response.data;
                            this.hv_reimb = true;
                        } else {
                            this.hv_reimb = false;
                        }
                    }
                )
        },
        async search_procurement(){
            var formData = new FormData();
            formData.append('key',this.procurement.tender);
            await axios.post('inventory/web/backend/search_procurement.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.proc_list = response.data;
                            this.hv_proc = true;
                        } else {
                            this.hv_proc = false;
                        }
                    }
                )

        },
        async search_supplier(){
            var formData = new FormData();
            formData.append('key',this.supplier.name);
            await axios.post('inventory/web/backend/search_supplier.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.supplier_list = response.data;
                            this.hv_supplier = true;
                        } else {
                            this.hv_supplier = false;
                        }
                    }
                )
        },
        async load_filter(){
            var formData = new FormData();
            //var section = document.getElementById('query').value;
            var section = this.item_code[0];
            formData.append('section',section);
            await axios.post('inventory/web/backend/filter_preload.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.category_list = response.data.category;
                            this.grp_list = response.data.grp_code;
                        }
                    }
                )
            document.getElementById('query').value = '';
        },
        async fetch_sub_category(){
            var formData = new FormData();
            formData.append('category',this.code.category);
            await axios.post('inventory/web/backend/fetch_sub_category.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.sub_category_list = response.data;
                            //this.code.sub_category = 'All';
                        } else {
                            this.sub_category_list = [];
                            //this.code.sub_category = 'All';
                        }
                    }
                )
        },
        async fetch_details(){
            var formData = new FormData();
            formData.append('item_code',this.item_code);
            formData.append('scope',this.scope);
            await axios.post('inventory/web/backend/fetch_details.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='timeout'){
                            this.$alert('Session time out');
                            this.$router.push('inventory_list');
                        } else if(response.data!='empty'){
                            this.details = response.data;
                            //code
                            this.code.barcode = response.data[0].barcode;
                            if(this.scope=='single'){
                                this.code.sn = response.data[0].sn;
                            }
                            this.code.category = response.data[0].category_code;
                            this.code.sub_category = response.data[0].sub_category_code;
                            //[END] code
                            //location
                            this.location.grp_code = response.data[0].grp_code;
                            this.location.building_code = response.data[0].building_code;
                            this.location.location_code = response.data[0].location_code;
                            this.location.sub_location_code = response.data[0].sub_location_code;
                            //[END]location
                            //product
                            this.product.eng_name = response.data[0].item_eng_name;
                            this.product.chi_name = response.data[0].item_chi_name;
                            this.product.eng_dp = response.data[0].eng_dpt;
                            this.product.chi_dp = response.data[0].chi_dpt;
                            if(this.scope=='single'){
                                this.product.brand = response.data[0].brand;
                            }
                            this.product.license = response.data[0].license;
                            this.product.ownership = response.data[0].ownership;
                            //[END]product
                            //Purchase
                            this.purchase.purchase_date = response.data[0].purchase_date;
                            if(this.scope=='single'){
                                this.purchase.warranty_exp = response.data[0].warranty_exp;
                            }
                            this.purchase.quantity = response.data[0].quantity;
                            this.purchase.total_cost = response.data[0].total_cost;
                            this.purchase.invoice = response.data[0].invoice;
                            //[END]Purchase
                            //Supplier
                            this.supplier.name = response.data[0].supplier;
                            this.supplier.supplier_contact = response.data[0].supplier_contact;
                            this.supplier.supplier_address = response.data[0].supplier_address;
                            //[END]Supplier
                            //Procurement
                            this.procurement.quot = response.data[0].quot;
                            this.procurement.tender = response.data[0].tender;
                            this.procurement.fundsrc = response.data[0].fundsrc;
                            this.procurement.unit_cost = response.data[0].unit_cost;
                            if(this.scope=='single'){
                                this.procurement.fundsrc_2 = response.data[0].fundsrc_2;
                                this.procurement.unit_cost_2 = response.data[0].unit_cost_2;
                            } else if(this.scope=='bulk'){
                                this.purchase.variance = response.data[0].variance_mngt;
                            }
                            //[END]Procurement
                            //Action
                            if(this.scope=='single'){
                                this.action.remarks = response.data[0].remarks;
                            }
                            this.action.stocktake = response.data[0].stocktake;
                            this.action.tag = response.data[0].tag;
                            this.action.maint_details = response.data[0].maintance_details;
                            //[END]Action
                        }
                    }
                )
        },
        return_prev(){
            this.$router.go(-1);
        },
        async handleSubmit(){
            var formData = new FormData();
            formData.append('scope',this.scope);
            formData.append('item_code',this.item_code);
            //Code
            formData.append('barcode',this.code.barcode);
            formData.append('sn',this.code.sn);
            formData.append('category',this.code.category);
            formData.append('sub_category',this.code.sub_category);
            //Location
            formData.append('grp',this.location.grp_code);
            formData.append('building',this.location.building_code);
            formData.append('location',this.location.location_code);
            formData.append('sub_location',this.location.sub_location_code);
            //Product
            formData.append('eng_name',this.product.eng_name);
            formData.append('chi_name',this.product.chi_name);
            formData.append('eng_dp',this.product.eng_dp);
            formData.append('chi_dp',this.product.chi_dp);
            formData.append('brand',this.product.brand);
            formData.append('license',this.product.license);
            formData.append('ownership',this.product.ownership);
            //Purchase
            formData.append('purchase_date',this.purchase.purchase_date);
            formData.append('warranty_exp',this.purchase.warranty_exp);
            formData.append('quantity',this.purchase.quantity);
            formData.append('total_cost',this.purchase.total_cost);
            formData.append('invoice',this.purchase.invoice);
            formData.append('variance',this.purchase.variance);
            //Supplier
            formData.append('supplier_name',this.supplier.name);
            formData.append('supplier_contact',this.supplier.supplier_contact);
            formData.append('supplier_address',this.supplier.supplier_address);
            //Procurement
            formData.append('quot',this.procurement.quot);
            formData.append('tender',this.procurement.tender);
            formData.append('fundsrc',this.procurement.fundsrc);
            formData.append('unit_cost',this.procurement.unit_cost);
            formData.append('fundsrc_2',this.procurement.fundsrc_2);
            formData.append('unit_cost_2',this.procurement.unit_cost_2);
            //Action
            formData.append('remarks',this.action.remarks);
            //formData.append('stocktake',this.action.stocktake);
            formData.append('tag',this.action.tag);
            formData.append('maintance',this.action.maint_details);
            await axios.post('inventory/web/backend/update_inv.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='OK'){
                            this.$alert('Successfully Updated Record: '+this.item_code);
                            this.$router.push('inventory_list');
                        } else {
                            this.$alert(response.data);
                        }
                    }
                )
        }
    }
}
</script>
<style scoped>
.flex{
    display:flex;
    flex-direction:row;
}
.px0{
    padding-left:0;
    padding-right:0;
}
.click_row{
    cursor: pointer;
}
u {
    color:rgba(0,0,0,0.6);
}
.cprow{
    margin-bottom:0.55rem;
}
td{
    text-transform: capitalize!important;
}
label{
    padding-top:0.35rem!important;
}
.inner_overlay{
    position:relative;
    margin-top:0;
    width:100%;
}
.cross_box{
    position:absolute;
    right:0;
    top:0;
}
.fa-window-close{
    transition: all 0.15s ease-in-out;
    color:rgba(255, 255, 255, 0.55);
}
.fa-window-close:hover{
    color:rgba(255, 255, 255, 1);
}
</style>


